import { createStore, applyMiddleware } from "redux";
import thunk from "redux-thunk";
import { composeWithDevTools } from "redux-devtools-extension";

import settings from "./settings/Reducer";
// ========================== app ====================== //
import { persistStore, persistCombineReducers } from "redux-persist";
import {
  authReducer,
  toastReducer,
  userReducer,
  mediaReducer,
  packageReducer,
  createMerchantByAdminReducer,
  getMerchantManagedByAdminReducer,
  getMerchantManagedByMerchantReducer,
  getAllMerchantsReducer,
} from "./reducer";

import storage from "redux-persist/lib/storage";
import {
  CreateCampaignReducer,
  DeleteCampaignReducer,
  getAllCampaignsReducer,
  UpdateCampaignReducer,
} from "./reducer/campaignReducer"; // defaults to localStorage for web

const persistConfig = {
  key: "root",
  storage,
  whitelist: ["auth"],
};

const rootReducer = {
  settings,
  allMerchants: getAllMerchantsReducer,
  merchantByAdmin: createMerchantByAdminReducer,
  getMerchantManagedByAdmin: getMerchantManagedByAdminReducer,
  getMerchantManagedByMerchants: getMerchantManagedByMerchantReducer,
  newCampaign: CreateCampaignReducer,
  allCampaigns: getAllCampaignsReducer,
  updateCampaign: UpdateCampaignReducer,
  deleteCampaign: DeleteCampaignReducer,
  user: userReducer,
  media: mediaReducer,
  auth: authReducer,
  toast: toastReducer,
  package: packageReducer,
};

const persistedReducer = persistCombineReducers(persistConfig, rootReducer);

export const store = createStore(
  persistedReducer,
  composeWithDevTools(applyMiddleware(thunk))
);
export const persistor = persistStore(store);
