import * as ActionTypes from "../actiontypes";

const initialState = {
  isProcessing: false,
  error: null,
  success_message: "",
};

export const CreateCampaignReducer = (state = initialState, action) => {
  switch (action.type) {
    case ActionTypes.ADD_CAMPAIGN_SUCCESS:
      return {
        isProcessing: false,
        error: null,
        success_message: action.payload,
      };

    case ActionTypes.ADD_CAMPAIGN_PROCESSING:
      return {
        isProcessing: true,
        error: null,
        success_message: "",
      };

    case ActionTypes.ADD_CAMPAIGN_FAILED:
      return {
        isProcessing: false,
        error: action.payload,
        success_message: "",
      };

    default:
      return state;
  }
};

// GET CAMPAIGNS
export const getAllCampaignsReducer = (
  state = { data: { data: [] } },
  action
) => {
  switch (action.type) {
    case ActionTypes.GET_ALL_CAMPAIGNS:
      return action.payload;

    default:
      return state;
  }
};

// UPDATE CAMPAIGN REDUCERS
export const UpdateCampaignReducer = (state = initialState, action) => {
  switch (action.type) {
    case ActionTypes.CAMPAIGN_UPDATED_SUCCESS:
      return {
        ...state,
        isProcessing: false,
        error: null,
        success_message: action.payload,
      };

    case ActionTypes.CAMPAIGN_UPDATED_PROCESSING:
      return { ...state, isProcessing: true, error: null, success_message: "" };

    case ActionTypes.CAMPAIGN_UPDATED_FAILED:
      return {
        ...state,
        isProcessing: false,
        error: action.payload,
        success_message: "",
      };

    default:
      return state;
  }
};

