import * as ActionTypes from "../actiontypes";

// GET ALL MERCHANTS
export const getAllMerchantsReducer = (
  state = { data: { data: [] } },
  action
) => {
  switch (action.type) {
    case ActionTypes.GET_ALL_MERCHANTS:
      return action.payload;

    default:
      return state;
  }
};

// MANAGED BY ADMIN

export const createMerchantByAdminReducer = (
  state = {
    isProcessing: false,
    error: null,
    success_message: "",
  },
  action
) => {
  switch (action.type) {
    case ActionTypes.CREATE_MERCHANT_BY_ADMIN_SUCCESS:
      return {
        ...state,
        isProcessing: false,
        error: null,
        success_message: action.payload,
      };

    case ActionTypes.CREATE_MERCHANT_BY_ADMIN_PROCESSING:
      return { ...state, isProcessing: true, error: null, success_message: "" };


      case ActionTypes.MERCHANT_UPDATED:
      return { ...state , data : {...state.data , data : state.data.data.filter(v => v._id != action.payload._id )} };

    case ActionTypes.CREATE_MERCHANT_BY_ADMIN_FAILED:
      return {
        ...state,
        isProcessing: false,
        error: action.payload,
        success_message: "",
      };

    default:
      return state;
  }
};

export const getMerchantManagedByAdminReducer = (
  state = { data: { data: [] } },
  action
) => {
  switch (action.type) {
    case ActionTypes.GET_MERCHANT_MANAGED_BY_ADMIN:
      return action.payload;

    default:
      return state;
  }
};

// MANAGED BY MERCHANTS

export const getMerchantManagedByMerchantReducer = (state = [], action) => {
  switch (action.type) {
    case ActionTypes.GET_MERCHANT_MANAGED_BY_MERCHANTS:
      return action.payload;

    default:
      return state;
  }
};
